import './topMenu.css'
import { NavLink } from 'react-router-dom';



export default function TopMenu() {
  return (
    <div className="topMenu">
      <div className="topMenuBox">
          <div className="topMenuList">
              <div className="topMenuItem">
                <NavLink to='/discoverMusic/recommend'></NavLink>
              </div>
              <div className="topMenuItem">
                <NavLink to='/discoverMusic/2'></NavLink>
              </div>
              <div className="topMenuItem">
                <NavLink to='/discoverMusic/playlist'></NavLink>
              </div>
              <div className="topMenuItem">
                <NavLink to="/discoverMusic/djRadio"></NavLink>
              </div>
              <div className="topMenuItem">
                <NavLink to='/discoverMusic/artist'></NavLink>
              </div>
              <div className="topMenuItem">
                <NavLink to="/discoverMusic/album"></NavLink>
              </div>
          </div>
      </div>
    </div>
  )
}