import request from './request'
/*  */
export function gotoPhoneLogin(phone: any, password: any) {
  return request({
    url: '/login/cellphone',
    method: 'get',
    params: {
      phone,
      password,
    },
  })
}

/*  */
export function gotoEmailLogin(email: any, password: any) {
  return request({
    url: '/login',
    method: 'get',
    params: {
      email,
      password,
    },
  })
}

// 
export function sendRegisterCode(phone: any) {
  return request({
    url: '/captcha/sent',
    method: 'get',
    params: {
      phone,
    },
  })
}

/*  */
export function sendRegister(captcha: any, phone: any, password: any, nickname: any) {
  return request({
    url: '/register/cellphone',
    method: 'get',
    params: {
      captcha,
      phone,
      password,
      nickname,
    },
  })
}
